@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,400i,700,700i&display=swap);
.popup-area{z-index:1001;box-shadow:0 19px 20px rgba(7,6,10,0.3),0 10px 12px rgba(0,0,0,0.22);border-radius:5px}.popup-area-active{position:absolute;top:50px;left:45px;background-color:#fff}.popup-menu{padding:15px;border-radius:5px;width:auto}.popup-menu .arrow-up{width:0;height:0;border-left:10px solid transparent;border-right:10px solid transparent;border-bottom:10px solid #fff;position:absolute;top:-10px}

